<template lang="html">
  <div class="img_wrapper" ref="box">
    <div class="progress" ref="progress" style="width:0%"></div>
    <div class="overlay"></div>

    <div class="logo" ref="logo">
      <div class="text">CLICILI</div>
    </div>
    <div class="img_box">
      <img  ref="img" :src="img.url" :height="height" :class="{'twoCols':Math.floor(index/2)==0,'threeCols':Math.floor(index/2)>0}" :index = "index" alt="img.id" v-for=" (img,index) in bannerImg">
    </div>

  </div>
</template>

<script>

export default {
  props: {
    bannerImg: Array
  },
  data () {
    return{
      logo: {url: '',text: 'CLICILI'},
      visible:  false,
    }
  },
  computed:{
    height () {
      let height = window.localStorage.getItem('height');
      return height/2;
    }
  },
  mounted () {
    this.$nextTick(() => {
    let loadedIndex = 0;
    this.$refs.img.forEach((x) => {
      x.onload = ()=> {
        loadedIndex++;
        this.$refs.progress.style.width = 16 + parseInt(this.$refs.progress.style.width)+'%';
        if(loadedIndex==this.bannerImg.length){
          this.$refs.progress.style.width = 100+'%';
          this.$refs.box.classList.add('fin');
        }
      }

    })


});

  }
}
</script>

<style lang="scss">
  @font-face {
    font-family:'Lato-Bold';
    src:url(../../static/fonts/Lato-Bold.ttf);
  }
  .img_wrapper{
    position: relative;
    width: 100%;
    height: 100%;
    overflow: hidden;
    >.overlay{
      position: absolute;
      top:0;
      left: 0;
      width: 100%;
      height: 100%;
      background: rgba(0,0,0,0.7);
    }

    >.progress{
      position: absolute;
      top: 0;
      left: 0;
      width: 0;
      height: 5px;
      background: #fff;
      z-index: 10;
    }

    >.logo{
      position: absolute;
      width: 100%;
      height: 100%;
      transition: all 0.5s cubic-bezier(.17, .67, .25, 1.02);
      background: #1fb5ad;
      background-position: center center;
      top:50%;
      left:50%;
      transform: translate(-50%,-50%);
      >.text{
        font-family: 'Lato-Bold';
        position: absolute;
        color:#fff;
        font-size: 50px;
        letter-spacing: 5px;
        top:50%;
        left:50%;
        transform: translate(-50%,-50%) scale(0.5);
        opacity: 0;
        }

      }




    .img_box{
      >img{
        display: block;
        float: left;
      }
      .twoCols{
        width: 50%;
      }
      .threeCols{
        width: 33.333333%;
      }
    }
    &.fin{
      >.progress{display: none;}
      >.logo{
        width: 390px;
        height: 108px;
        >.text{
          transform: translate(-50%,-50%) scale(1);
          opacity: 1;
        }
      }
  }
}
</style>



